.net-editor {
  width: 300px;
  height: calc(100vh - 88px);
  padding: 10px 40px 10px 40px;
  background-color: #f8f8f8;
  box-shadow: 2px 2px 8px #bbb;
  overflow: scroll;
}
.net-editor .editor-title {
  margin-top: 8px;
  margin-bottom: 5px;
  font-size: 15px;
}
.net-editor .attribute-item {
  margin: 5px 0 5px 0;
}
.net-editor .attribute-item .attribute-item-name {
  font-size: 13px;
}
.net-editor .style-panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 5px;
  margin-bottom: 10px;
}
.net-editor .style-panel .style-option {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.net-editor .style-panel .style-option img {
  display: inline-block;
  height: 60px;
  width: 60px;
  border: 2px solid rgba(0, 0, 0, 0.1);
}
.net-editor .style-panel .style-option .text {
  margin-top: 5px;
  font-size: 13px;
}
.net-editor .style-panel .style-option-checked img {
  border-color: #F28B30;
}
.net-editor .layout-panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 5px;
  margin-bottom: 10px;
}
.net-editor .layout-panel .layout-option {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.net-editor .layout-panel .layout-option img {
  display: inline-block;
  max-height: 100px;
  max-width: 100px;
  border: 2px solid rgba(0, 0, 0, 0.1);
}
.net-editor .layout-panel .layout-option-checked img {
  border-color: #F28B30;
}
